:root {
  --main-bg-color: #282c34;
}

html {
  background-color: var(--main-bg-color);
}

.thisApp {
  text-align: center;
  background-color: var(--main-bg-color);
  color: rgb(32, 148, 184);
  min-height: 100vh;
}

.customNav{
  background-color : #1f396e;
}
.active{
  color : black;
  background-color:pink;
}
.App-logo {
  height: 5vmin;
  pointer-events: none;
  shape-outside: circle();
}
.Logo-div {
  /* background-color: #282c34; */
}

.App-header {
  background-color: var(--main-bg-color);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(8px + 2vmin);
  color: rgb(32, 148, 184);
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-trans {
  from {
    transform: translate(0, -3vh) rotate(0deg);
  }
  to {
    transform: translate(0vw, 3vh) rotate(360deg);
  }
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo-home {
    animation: infinite alternate 10s linear App-logo-spin;
    animation-timing-function: ease;
  }
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 10s linear;
  }
}

@media (prefers-reduced-motion: no-preference) {
  .mooving-logo {
    animation: App-logo-trans infinite 3s linear alternate;
    /* animation-timing-function: ease; */
  }
}

.App-logo-home {
  height: 120px;
  pointer-events: none;
  shape-outside: url(../assets/logo.svg);
  float: left;
}

.btn {
  border : 5em;
  border-radius: 1rem;
}

footer{
  height: 300px;
  background-color: rgb(22, 20, 20);
  background-image: linear-gradient(0deg, #1e14d4 0%, #090979 50%, var(--main-bg-color) 100%);
}