.LoginContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 80vh;
  width: 100%;
}

.LoginFormBox {
    background-color:rgb(14, 41, 49);
    padding:20px;
    display:flex;
    flex-direction:column;
    justify-content:flex-start;
    opacity:1;
    border-radius:2%;
    width: 50vw;
    min-width: 15em;
    max-width: 20em;
}

div.LoginContainer h2{
  color: rebeccapurple;
}

button.Login:hover {
  color: rgb(128, 128, 128);
  transition: 0.6s;
}

.btn-disabled {
  color: red;
}

.btn-enabled {
  color: rgb(43, 177, 94);
}
